import React from 'react';
import {Component} from 'react';
import Card from 'react-bootstrap/Card';
import CardDeck from 'react-bootstrap/CardDeck'
import Button from 'react-bootstrap/Button';

class Projects extends Component{

    render(){
        return(
            <div className="project_page">
                <CardDeck>
                <Card>
                    <Card.Body>
                    <Card.Title>Welcome to my Projects!</Card.Title>
                    <Card.Text>
                        More projects will be added as I gather all my github links and deploy the repositories. You can check all my projects 
                        using the github button on the links page.{' '}
                    </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <small className="text-muted">Last updated 3/4/2021</small>
                    </Card.Footer>
                </Card>
                <Card>
                    <Card.Body>
                    <Card.Title>ECampus</Card.Title>
                    <Card.Text>
                        This is communication system that runs on Firebase and React and allows for students and professionals
                        to communicate with each other and have a place to manage the project status, and for
                        user groups that are create towards each organization. The build does not show full functionality (as the project page doesn't show),
                        however is fully functional on local deployment.
                    </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <small className="text-muted">Last updated 3/14/2021</small>
                    </Card.Footer>
                    <Button href="https://elastic-euclid-62f3b2.netlify.app/" target="_blank"> ECampus </Button>
                </Card>
                <Card>
                    <Card.Body>
                    <Card.Title>Valorant Randomizer</Card.Title>
                    <Card.Text>
                        This is an iOS application that randomizes agents, weapons, and strategies for the game Valorant by Riot Games. 
                        Coded in Swift, the application makes your custom games with friends become more daring. Application is not out on
                        the play store, however you can download it via the Download link below.
                    </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <small className="text-muted">In Progress...</small>
                    </Card.Footer>
                    <Button href="/" target="_blank"> Download </Button>
                </Card>
                </CardDeck>

            </div>
        )
    }
}


export default Projects;