import React from 'react';
import {Component} from 'react';
import Button from '@material-ui/core/Button';

class Links extends Component{

    render(){
        return(
            <div className="link_page">

                <div className="top_layout">
                    <img className="me_links" src="https://i.imgur.com/6MScSSi.jpg"/>
                </div>

                <h3>@r.jamkatel</h3>
                <hr></hr>
                <div className="button-list">
                    <Button variant="contained" color="primary" size="large" href="http://www.github.com/roshanjamkatel">Github</Button><hr></hr>
                    <Button variant="contained" color="primary" size="large" href="http://www.twitch.tv/calatlasbb">Twitch</Button><hr></hr>
                    <Button variant="contained" color="primary" size="large" href="http://www.anchor.fm/the-floatcast">The Floatcast</Button><hr></hr>
                    <Button variant="contained" color="primary" size="large" href="http://www.instagram.com/r.jamkatel">Personal Instagram</Button><hr></hr>
                    <Button variant="contained" color="primary" size="large" href="http://www.instagram.com/rjamkatelphotography">Photography Instagram</Button><hr></hr>
                    <Button variant="contained" color="primary" size="large" href="https://blog.rjamkatel.com/">BLOG</Button><hr></hr>
                </div>
            </div>
        )
    }
}


export default Links;