import React from 'react';
import {Component} from 'react';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';

class About extends Component{

    render(){
        return(
            <div className="about_page">
                <center>
                <Card style={{ width: '18rem' }}>
                    <Card.Img variant="top" src="https://i.imgur.com/6MScSSi.jpg" />
                    <Card.Body>
                        <Card.Title>Roshan Jamkatel</Card.Title>
                            <Card.Text>
                                Hello, I'm a senior at DePaul University who loves to talk about tech, and learn about new 
                                technologies! Click my Links and Project tabs to learn about what I've done!
                            </Card.Text>
                        <Button variant="primary" href="rjamkatel_resume_2021.pdf" download>My Resume</Button>
                        </Card.Body>
                </Card>
                </center>
            </div>
        )
    }
}


export default About;